#drum-machine {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}

#container {
  background-color: rgba(255, 235, 205, 0.808);
  display: grid;
  grid-template-columns: 15em 15em 15em;
  grid-column-gap: 2em;
  grid-row-gap: 2em;
}

.drum-pad {
  text-align: center;
  border: 1px solid black;
  padding: 75px;
}

#display {
  margin-top: 10px;
  font-size: 20px;
  font-weight: 1000;
}